import type { PropsWithChildren } from "react";

type ClassNames = string;

const Paragraph = ({
  children,

  className,
}: PropsWithChildren<{ className?: ClassNames }>) => (
  <p
    className={`text-l md-max:text-m font-medium text-c4 text-shadow sm-max:text-sm ${
      className ?? ""
    }`}
  >
    {children}
  </p>
);

export { Paragraph };
