import type { PropsWithChildren } from "react";

type ClassNames = string;

const Bold = ({
  children,

  extraClassNames,
}: PropsWithChildren<{ extraClassNames?: ClassNames }>) => (
  <span
    className={`font-semibold text-c2-light dark:text-c5-light ${
      extraClassNames ?? ""
    }`}
  >
    {children}
  </span>
);

export { Bold };
