import type { PropsWithChildren, ReactNode } from "react";

enum Sizes {
  H1 = "h1",
  H2 = "h2",
  H3 = "h3",
  H4 = "h4",
}

type ClassNames = string;
type TitleSizesArgs = { children: ReactNode; className?: ClassNames };

const commonStyle = "font-semibold text-c2-light dark:text-c5-light";

const TitleSizes = {
  [Sizes.H1]: ({ children, className }: TitleSizesArgs) => (
    <h1
      className={`${commonStyle} text-6xl md-max:text-4xl ${className ?? ""}`}
    >
      {children}
    </h1>
  ),
  [Sizes.H2]: ({ children, className }: TitleSizesArgs) => (
    <h2
      className={`${commonStyle} text-4xl md-max:text-2xl ${className ?? ""}`}
    >
      {children}
    </h2>
  ),
  [Sizes.H3]: ({ children, className }: TitleSizesArgs) => (
    <h3 className={`${commonStyle} text-2xl md-max:text-xl ${className ?? ""}`}>
      {children}
    </h3>
  ),
  [Sizes.H4]: ({ children, className }: TitleSizesArgs) => (
    <h4 className={`${commonStyle} text-l md-max:text-m ${className ?? ""}`}>
      {children}
    </h4>
  ),
};

const Title = ({
  children,
  size,
  className,
}: PropsWithChildren<{ className?: ClassNames; size: Sizes }>) => {
  return TitleSizes[size]({ children, className });
};

export { Title, Sizes };
