import { useState, useCallback } from "react";
import Typist from "react-typist-component";
import { BlobbyPhoto } from "~/components/BlobbyPhoto";
import { Card } from "~/components/Card";
import { Social } from "~/components/Icons";
import type { SocialProps } from "~/components/Icons/Social";
import { Title, Paragraph, Bold } from "~/typography";
import { Sizes } from "~/typography/Title";

const SocialIcons = ({ icons }: { icons: SocialProps[] }) => {
  return (
    <div className="flex min-w-full justify-end">
      <div className="md-max:w-sm flex w-[170px] justify-between">
        {icons.map((icon: SocialProps) => (
          <Social key={icon.website} {...icon} />
        ))}
      </div>
    </div>
  );
};

const SocialCard = ({ icons }: { icons: SocialProps[] }) => {
  const [cursor, setCursor] = useState("|");
  const removeCursor = useCallback(() => setCursor(""), []);

  return (
    <div className="social flex basis-1/2 flex-col items-center justify-center p-8 md-max:absolute md-max:mt-[35%] md-max:min-w-full">
      <Card extraClassNames="social-card md-max:bg-opacity-80 md-max:social-card--md">
        <Title
          size={Sizes.H1}
          className="social-title mb-2 text-justify md-min:mb-4"
        >
          Diogo Parente
        </Title>
        <Paragraph className="mb-6 md-max:text-c5 dark:md-max:text-c4 sm-min:h-8 md-min:mb-6 md-min:h-12 lg-min:h-16">
          <Typist cursor={cursor} onTypingDone={removeCursor}>
            Building things for the web, <br className="md-min:hidden" />
            one <Bold>bug</Bold>
            <Typist.Delay ms={250} />
            <Typist.Backspace count={1} />
            <Typist.Delay ms={250} />
            <Typist.Backspace count={1} />
            <Typist.Delay ms={250} />
            <Typist.Backspace count={1} />
            <Bold>line</Bold> at a time
          </Typist>
        </Paragraph>
        <SocialIcons icons={icons} />
      </Card>
    </div>
  );
};

const SocialSection = ({
  icons,
  photo,
}: {
  icons: SocialProps[];
  photo: string;
}) => (
  <div className="welcome-background relative flex max-h-[50rem] text-c4 dark:text-c5 md-max:max-h-full md-max:flex-col">
    <BlobbyPhoto blob={"blob_1"} imgSrc={photo} alt="diogo-standing" />
    <SocialCard icons={icons} />
  </div>
);

export default SocialSection;
