import React from "react";

export type IconProps = {
  website: string;
  name: string;
};

const Icon: React.FC<IconProps> = ({ name, website }) => (
  <a
    href={website}
    aria-label={name}
    target="_blank"
    rel="noreferrer"
    className="justify-self-center"
  >
    <svg
      width="100%"
      height="100%"
      viewBox="0 0 24 24"
      xmlns="http://www.w3.org/2000/svg"
      version="1.1"
      preserveAspectRatio="xMinYMin"
    >
      <use xlinkHref={name} />
    </svg>
  </a>
);

export { Icon };
